import httpClient from "src/utils/httpClient";

const uploadFile = async (file) => {
  const data = new FormData();
  data.append("file", file);
  return await httpClient.post("/api/v1/admin/uploads", data);
};

const getUploadedFiles = async () => await httpClient.get("/api/v1/admin/uploads");

const deleteFile = async (id) => await httpClient.delete(`/api/v1/admin/uploads/${id}`)

const UploadService = {
  uploadFile,
  getUploadedFiles,
  deleteFile,
};


export default UploadService;
